import Vue from 'vue'
import Router from 'vue-router'
import Board from '@/components/Board.vue'
Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Board',
      component: Board
    },
  ]
})



